import React from "react";
// import image from "./images.json";
// import { CLICKCOUNT } from "./Counter";

const image = [
  { "id": 1, "clicks": 0, "src": "./images/img0.png" },
  { "id": 2, "clicks": 0, "src": "./images/img1.png" },
  { "id": 3, "clicks": 0, "src": "./images/img2.png" },
  { "id": 4, "clicks": 0, "src": "./images/img3.png" },
  { "id": 5, "clicks": 0, "src": "./images/img4.png" },
  { "id": 6, "clicks": 0, "src": "./images/img5.png" },
  { "id": 7, "clicks": 0, "src": "./images/img6.png" },
  { "id": 8, "clicks": 0, "src": "./images/img7.png" },
  { "id": 9, "clicks": 0, "src": "./images/img8.png" },
  { "id": 10, "clicks": 0, "src": "./images/img9.png" },
  { "id": 11, "clicks": 0, "src": "./images/img10.png" },
  { "id": 12, "clicks": 0, "src": "./images/img11.png" }
];
let counter = 0;


class Images extends React.Component {
  constructor() {
    super();

    this.state = {
      image,
      counter
    };

  }
       
  handleClick = () => {
    this.setState((prevState) => ({
      counter: prevState.counter + 1 
    }));
  };


  render() {
    return (

      <div className="text-center">
      
      <div className="card-header bg-primary text-white">
      <h2>Click Counter</h2>
      <p>{this.state.counter}</p>
 
</div>
<br />


      <div className="card-deck col-xl-6 mx-auto">

        {image.map(image => (
          <div className="col-lg-3" key={image.id}>
            <div className="card shadow p-2 bg-white rounded mb-4">

            <div style={ { backgroundImage: `url(require({image.src}))` } } />



            {/* <div className="card-img" style="background-image: url({image.src};" onClick={this.handleClick} /> */}

              <img className="card-img" src={image.src} alt="img" onClick={this.handleClick} />

            </div>
          </div>


        ))}

      </div>
      </div>


    );
      }
    }
    
    export default Images;
